<template>
  <div class="publish-container">
  <div class="publish-score">
    <ul class="listview listview-form">
      <li style="border-top:none;border-bottom:1px solid #e1e1e1;" @click="goTestScore()">
        <label>考试成绩</label>
        <input  style="border-bottom: none" id="leave-type" v-model="getScoreValue" type="text" placeholder="请选择考试成绩"
               readonly/>
        <img src="../assets/arrow-icon.png"/>
        <p></p>
      </li>
      <li style="border:none" @click="goRange">
        <label>发布范围</label>
        <input  style="border-bottom: none" v-model="getScoreRangeValue" type="text" placeholder="请选择发布范围" readonly/>
        <img src="../assets/arrow-icon.png"/>
        <p></p>
      </li>
    </ul>
    <div class="mar-top">
        <v-switch style="padding-left: 0.4rem" class="backwhite" @input="getStatus" label="需回执" v-model="isReceive"></v-switch>
    </div>
    <div v-show="receiveTime" class="bor select-time" @click="openPicker('2')">
      <!--<label></label>-->
      <span class="select-text">选择日期</span>
      <!--<input style="border-bottom: none" @input="validateInput" type="text" v-model="endTime" readonly />-->
      <span class="select-date-sp">{{endTime}}</span>
      <img src="../assets/arrow-icon.png"/>
      <p class="select-txt">选填</p>
    </div>
    <v-datetime-picker
      ref="picker5"
      type="smart"
      v-model="value5"
      :visibleItemCount="5"
      @confirm="handlePicker5"
    ></v-datetime-picker>
    <div class="mar-top ranking">
      <v-switch style="background:#fff; padding-left: 0.4rem" label="包含考试排名" v-model="isRange"></v-switch>
    </div>
    <div class="footer-btn" style="border-top: 1px solid #e1e1e1;" v-if="!canSubmit">
      <button>发布</button>
    </div>
    <div class="footer-btn" style="border-top: 1px solid #e1e1e1;"  v-if="canSubmit" @click="publish()">
      <button :class="{'btn': canSubmit}">发布</button>
    </div>
    <!--发布成功弹框-->
    <div v-show="shoadowShow" class="shadow">
      <em></em>
      <p>发布成功</p>
    </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import api from '../utils/api';
  import vCell from '../vendor/v-cell.vue';
  import vDatetimePicker from '../vendor/v-datetime-picker';
  import vSwitch from '../vendor/v-switch.vue';
  import logger from '../js/utils/logger';
  import utils from '../js/utils/utils';
  import { mapGetters, mapMutations } from 'vuex';
  import Toast from '../vendor/v-toast';

  export default {
    name: 'publish-score',
    components: {'v-cell': vCell, 'v-switch': vSwitch, vDatetimePicker},
    data () {
      return {
        isRelease: false, // 限制多次提交
        isPublish: false, // 表示是否已经发布
        shoadowShow: false, // 发布成功toast
        title: '考试成绩',
        isReceive: false, // 是否回执
        isRange: false, // 考试排名
        receiveTime: false, // 回执时间是否显示
        endTime: '', // 选择截止日期
        value5: ''
      };
    },
    created () {
      this.text = '';
      this.SET_SCORE_RANGE_VALUE('');
      this.isReceive = false;
      this.isRange = false;
    },
    computed: {
      ...mapGetters(['getRange', 'getExamId', 'getScoreValue', 'getScoreRangeValue', 'getOriginType']),
      canSubmit () {
        if (!this.getScoreRangeValue && !this.getScoreValue) {
          return false;
        } else {
          return (this.getScoreRangeValue && this.getScoreValue);
        }
      }
    },
    mounted () {
    },
    activated () {
      this.isRelease = false;
      this.isPublish = !this.getScoreRangeValue.length;
      // 获取当前发布页是从哪个页面过来的
      this.$route.query.origin = this.getOriginType;
      if (this.$route.query.origin === 'listPage') {
        this.SET_SCORE_VALUE(''); // 清空考试科目内容
        this.SET_SCORE_RANGE_VALUE(''); // 清空发布范围值
        this.isReceive = false; // 恢复回执按钮不选中
        this.receiveTime = false; // 隐藏回执时间
        this.isRange = false; // 恢复考试排名不选中
        this.endTime = '';
      }
    },
    watch: {
    },
    methods: {
      ...mapMutations(['SET_SCORE_VALUE', 'SET_SCORE_RANGE_VALUE']),
      goTestScore () { // 发布成绩
        this.$router.push({path: '/testScore', query: ''});
      },
      goRange () { // 发布成绩
        this.$router.push({path: '/searchResult', query: {'pushType': 2, 'isPublish': this.isPublish, 'noType': this.$route.query.noType}});
      },
      validateInput () {
      },
      openPicker (is) {
        this.value5 = new Date();
        this.$refs.picker5.open();
        this.startOrEnd = is;
      },
      handlePicker5 (value) {
        this.value5 = value;
        this.endTime = utils.formatTime(value.getTime(), 'yyyy-MM-dd hh:mm');
      },
      // 是否回执按钮
      getStatus (val) {
        console.log(val);
        if (val === true) {
          this.receiveTime = true;
        } else {
          this.receiveTime = false;
        }
      },
      publish () {
        let vm = this;
        if (this.isRelease) {
          return;
        }
        this.isRelease = true;
        if (!vm.canSubmit) return;
        // const timestamp = (new Date(this.endTime)).valueOf(); // 时间戳转换的
        const timestamp = +new Date(this.endTime.replace(/-/g, '/')); // 时间戳转换的 兼容ios
        let receiveStatus = 0;
        this.isReceive ? receiveStatus = 1 : receiveStatus = 0;
        let rangeStatus = 0;
        this.isRange ? rangeStatus = 1 : rangeStatus = 0;
        let param = {
          notice: {
            noContent: '',
            noPicture: 0,
            noReceipt: receiveStatus,
            noPublisherId: '',
            noReceiptTime: timestamp,
            noTitle: '',
            noType: this.$route.query.noType,
            noSubject: '',
            noExamId: this.getExamId,
            noRanking: rangeStatus
          },
          noticePictures: [],
          noticeRanges: vm.getRange
        };
        if (!vm.getScoreValue) Toast('请选择考试成绩');
        if (!vm.getScoreRangeValue) Toast('请选择发布范围');
        api.publishNotice(JSON.stringify(param)).then((res) => {
          if (res.data.code - 0 === 0) {
            // logger.log(res.data, '发布成发布成绩发布成绩发布成绩绩'); // 发布成绩
            vm.shoadowShow = true;
            setTimeout(() => {
              vm.shoadowShow = false;
              this.SET_SCORE_VALUE('');
              this.SET_SCORE_RANGE_VALUE('');
              vm.endTime = '';
              vm.isRange = false;
              vm.isReceive = false;
              vm.$router.replace({path: '/'});
            }, 500);
          }
        }).catch((err) => {
          this.isRelease = true;
          logger.log(err);
        });
      }
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "../scss/variables";
  @import "../scss/mixins";

  $line-height: pxTorem(44px);
  $line-height-double: pxTorem(64px);
  $tit-color: #383838;
  $tit-color-light: #737373;
  $placeholder-color: #007AFF;
  $color-grey: #d9d9d9;
  $color-white: #FFF;
  $color-blue: #4CD864;
  .publish-container{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #f2f2f4;
    overflow: hidden;
    .publish-score {
      width: 100%;
      background: #FFF;
      border-top: 1px solid #e1e1e1;/*no*/
      /*border-bottom: 1px solid #e1e1e1;!*no*!*/
      .mar-top {
        /*margin-left: 15px;*/
        padding-right: 0;
        background:#f2f2f4;
        padding-top:10px;
      }
      .ranking label.v-switch {
        border-bottom: none !important;
      }

      .backwhite{
        background:#fff;

      }
      body {
        background: #f2f2f4!important;
      }
      ul {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding-left: 15px;
        background: #ffffff;

        li {
          width: 100%;
          position: relative;
          box-sizing: border-box;
          margin-right: 0;
          label {
            width: 20%;
            display: inline-block;
            font-size: pxTorem(15px);
            line-height: pxTorem(15px);
            color: #383838;
          }
          input {
            display: inline-block;
            width: 65%;
            height: pxTorem(44px);
            border-bottom: none !important;
            padding: pxTorem(13px) pxTorem(23px) pxTorem(13px) 10px;
            box-sizing: border-box;
            font-size: pxTorem(15px);
            line-height: pxTorem(16px);
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          img {
            width: pxTorem(6px);
            height: pxTorem(10px);
            position: absolute;
            right: 20px;
            top: pxTorem(19px);
          }
          p {
            width: pxTorem(285px);
            height: pxTorem(1px);
            /*border-bottom: 1px solid #DDDEE3;*/
            position: absolute;
            bottom: 0;
            right: 0;
          }

        }
        li:first-child {
          border-bottom: 1px solid #DDDEE3;/*no*/
          input {
            border-bottom: 0;
          }
        }

        .v-switch {
          position: relative;
          height: 100%;
          @include box_flex;
          @include align_items(center);
          @include justify-content(space-between);
        }
        .v-switch__label {
          height: 100%;
          font-size: 15px;
          line-height: $line-height;
          color: $tit-color;
          padding-left: 14px!important;
          @include flex_grow(0);
          @include flex_shrink(0);
          @include flex_basis(auto);

          &.light {
            color: $tit-color-light;
          }
        }
        .v-switch__input {
          display: block;
          padding-left: 240px;
          &:checked {
            + .v-switch__core {
              border-color: $color-blue;
              background-color: $color-blue;
            }
          }
        }
        .v-switch__core {
          width: 30%;
          margin-right: pxTorem(15px);
          display: inline-block;
          position: relative;
          /*width: pxTorem(41px);*/
          height: pxTorem(25px);
          border: pxTorem(1px) solid #e1e1e1;
          border-radius: pxTorem(16px);
          box-sizing: border-box;
          background: #FFF;
          @include flex_grow(0);
          @include flex_shrink(0);
          @include flex_basis(auto);
        }
      }
      .publish-score ul li:first-child{
        border-bottom: 1px solid #e1e1e1!important;/*no*/
      }
      .footer-btn{
        width: 100%;
        height: 54px;
        padding: 8px 30px;
        box-sizing: border-box;
        background: #FFFFFF;
        position: fixed;
        left: 0;
        bottom: 0;
        background: rgba(255,255,255,0.90);
        box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
        border-top: 1px solid #e1e1e1;/*no*/
        overflow: hidden;
        .btn {
          background: #C3303A;
        }
        button{
          width: 315px;
          height: 38px;
          font-size: pxTorem(17px);
          line-height: pxTorem(38px);
          text-align: center;
          color: #FFFFFF;
          background: #DDDEE3;
          border-radius: pxTorem(4px);
        }

      }
      .shadow {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -60px;
        margin-left: -60px;
        width: 120px;
        height: 120px;
        background: #000000;
        opacity: 0.5;
        border-radius: 5px;
        em {
          margin: 15px 0 0 35px;
          /*padding:15px 35px 0 35px;*/
          display: inline-block;
          width: 50px;
          height: 50px;
          background: url('../assets/toast-rig.png') no-repeat center;
          background-size: 100%;
        }
        p {
          margin-left: 35px;
          font-size: 13px;
          color: #FFFFFF;
          letter-spacing: 0;
        }
      }

    }
    .bor {
      position: relative;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding-left: 15px;
      background: #fff;
      padding-right: 0;
      border-top: 1px solid #e1e1e1;/*no*/
      border-bottom: 1px solid #e1e1e1;/*no*/
      label {
        width: 20%;
        display: inline-block;
        font-size: pxTorem(15px);
        line-height: pxTorem(15px);
        color: #383838;
      }
      input {
        display: inline-block;
        width: 65%;
        height: pxTorem(44px);
        border-bottom: none !important;
        /*padding: pxTorem(13px) pxTorem(23px) pxTorem(13px) 10px;*/
        box-sizing: border-box;
        font-size: pxTorem(15px);
        line-height: pxTorem(16px);
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      .select-text{
        padding-top: 2px;
        float: left;
        width: 20%;
        font-size: pxTorem(15px);
        height: 42px;
        line-height: 42px;
        color: #383838;
      }
      .select-date-sp{
        padding-top: 2px;
        padding-left: 13px;
        display: inline-block;
        width: 65%;
        height: pxTorem(42px);
        line-height: pxTorem(42px);
        border-bottom: none !important;
        /*padding: pxTorem(13px) pxTorem(23px) pxTorem(13px) 10px;*/
        box-sizing: border-box;
        font-size: pxTorem(15px);
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      img {
        width: pxTorem(6px);
        height: pxTorem(10px);
        position: absolute;
        right: 20px;
        top: pxTorem(19px);
      }
      p {
        width: pxTorem(285px);
        height: pxTorem(1px);
        /*border-bottom: 1px solid #DDDEE3;*/
        position: absolute;
        bottom: 0;
        right: 0;
      }
      &.select-time{
        border: none;
      }
    }
    .select-txt {
      position: absolute;
      top: 16px;
      left: 315px;
      font-size: 15px;
      width: auto;
      color: #777E8C;
      line-height: 15px;
    }
  }
</style>




